html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

a{transition:all 0.3s}a.bold{font-weight:bold}a.main{color:#18da66}a.main:hover{color:#3cc171}a.companion{color:#ee4f3e}a.companion:hover{color:#aa2011}.mt-1{margin-top:1rem}.mt-2{margin-top:2rem}.main-logo{width:240px}.body-container{margin-top:2rem}.image{display:flex;flex:0.4 1;align-items:center;justify-content:center}.image img{width:unset}.text{flex:0.6 1;display:flex;flex-flow:column nowrap}.text #text-uml{width:100%;background-color:transparent;font-size:1rem;min-height:40vh;padding:0;margin:0;padding:0.5rem;border-radius:10px;outline:none}.text #text-uml:focus{box-shadow:0px 0px 2px 2px rgba(238,79,62,0.3);border:1px solid rgba(238,79,62,0.3)}.text .is-action{transition:all .3s;margin:1rem 0;border:1px solid #18da66;background-color:#18da66;color:#fff;font-size:1.5rem;outline:none;box-shadow:none !important;border-radius:10px}.text .is-action:hover{background-color:#3cc171}@media screen and (max-width: 768px){.body-container{flex-flow:column nowrap}}.ab-title{display:flex;align-items:center}.ab-title .text{margin-left:.25rem}.ab-title .text.text-b{color:#000}.ab-title.lit{color:#ee4f3e;font-size:1.3rem}.ab-title.big{color:#18da66;font-size:1.5rem}

